<template>
  <div class="product-detail-page">
    <div v-if="product" class="product-container">
      <!-- Hình ảnh sản phẩm nằm bên trái -->
      <div class="image-slider">
        <div class="image-slide">
          <img :src="`http://localhost:5000/uploads/${product.image}`" alt="Product Image" class="product-image" />
        </div>
      </div>

      <!-- Thông tin sản phẩm nằm bên phải -->
      <div class="product-info">
        <h2>{{ product.name }}</h2>
        <p>{{ product.description }}</p>
        <p>Price: ${{ product.price }}</p>
        <p>Material: {{ product.material }}</p>
        <p v-if="product.quantity > 0">Quantity Available: {{ product.quantity }}</p>
        <p v-else class="out-of-stock">Out of Stock</p>

        <!-- Input số lượng -->
        <div class="quantity-group">
          <label for="quantity">Quantity:</label>
          <input type="number" v-model="quantity" min="1" :max="product.quantity" />
        </div>

        <!-- Nút Thêm vào giỏ hàng và Mua ngay -->
        <div class="action-buttons">
          <button class="add-to-cart" @click="checkAuth('addToCart')">Add to Cart</button>
          <button class="buy-now" @click="checkAuth('buyNow')">Buy Now</button>
        </div>
      </div>
    </div>

    <!-- Hiển thị khi sản phẩm đang load -->
    <div v-else>
      <p>Loading product details...</p>
    </div>

    <!-- Modal đăng nhập -->
    <LoginPrompt :show="showLoginPrompt" @close="showLoginPrompt = false" />
  </div>
</template>

<script>
import LoginPrompt from '@/views/LoginPrompt.vue';

export default {
  data() {
    return {
      product: null, // Khởi tạo với null
      quantity: 1, // Số lượng sản phẩm
      showLoginPrompt: false // Điều khiển hiển thị modal
    };
  },
  components: {
    LoginPrompt
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters.isAuthenticated; // Kiểm tra trạng thái đăng nhập
    }
  },
  async created() {
    const productId = this.$route.params.id;
    try {
      const response = await fetch(`http://localhost:5000/api/furniture/${productId}`);
      if (response.ok) {
        this.product = await response.json();
      } else {
        console.error('Failed to fetch product details');
      }
    } catch (error) {
      console.error('Error fetching product details:', error);
    }
  },
  methods: {
        checkAuth(action) {
    if (!this.isAuthenticated) {
      this.showLoginPrompt = true; // Hiển thị modal nếu chưa đăng nhập
    } else {
      if (action === 'addToCart') {
        this.addToCart();
      } else if (action === 'buyNow') {
        this.buyNow();
      }
    }
  },
  addToCart() {
    const productToAdd = {
      _id: this.product._id,
      name: this.product.name,
      price: this.product.price,
      material: this.product.material,
      image: this.product.image,
      quantity: this.quantity
    };
    this.$store.dispatch('addToCart', productToAdd); // Dispatch action để thêm sản phẩm vào giỏ
  },
    async buyNow() {
      try {
        const response = await fetch(`http://localhost:5000/api/furniture/buy/${this.product._id}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            quantity: this.quantity
          })
        });

        if (response.ok) {
          console.log(`Bought ${this.quantity} ${this.product.name}`);
        } else {
          console.error('Failed to buy product');
        }
      } catch (error) {
        console.error('Error buying product:', error);
      }
    }
  }
};
</script>



